.menu {
  height: 100vh;
  background-color: $c-black;

  &__nav {
    padding-top: 65px;

    @include onTablet {
      padding-top: 123px;
    }
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 41px;

    &--link {
      width: 100%;
      color: $c-white;
      position: relative;

      &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: -10px;
        z-index: 99;

        width: 100%;
        height: 1px;

        background-color: #828282;
      }
    }
  }
}
